@import '../../scss/mixin.scss';
header.worldie-header {
    position: sticky;
    top: 0;
    z-index: 999;
    background: linear-gradient(90deg, rgba(2, 107, 235, 1) 0%, rgba(180, 199, 229, 1) 50%, rgba(164, 102, 225, 1) 100%);
    .title {
        padding: 0px 16px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &>.logo {
            width: 150px;
            height: 51px;
            display: grid;
            cursor: pointer;
            &::before {
                content: '';
                background: no-repeat center/contain url('/assets/images/logo.png');
                padding-top: 30%;
            }
        }
        &>.action {
            display: grid;
            grid-auto-flow: column;
            gap: 6px;
            align-items: center;
            padding: 0px;
            &>.login,
            &>.sign-up {
                font-family: 'Open Sans', sans-serif;
                font-size: 14px;
                font-weight: 600;
                color: #0BA59B;
                height: 33px;
                width: 100px;
                display: flex;
                justify-content: center;
                align-items: center;
                border: 1px solid white;
                border-radius: 33px;
                text-transform: uppercase;
                font-weight: bold;
                cursor: pointer;
            }
            &>.login {
                background-color: transparent;
                color: white;
                &:hover {
                    color: black;
                }
            }
        }
    }
    &>.menu-container {
        background-color: white;
        &>.menu {
            display: flex;
            justify-content: space-between;
            box-shadow: rgb(0 0 0 / 20%) 0px 2px 1px -1px, rgb(0 0 0 / 14%) 0px 1px 1px 0px, rgb(0 0 0 / 12%) 0px 1px 3px 0px;
            padding: 0 15px;
            &>li {
                color: attr(color);
                width: fit-content;
                cursor: pointer;
                &>.content {
                    display: grid;
                    gap: 5px;
                    padding: 7px 12px;
                    grid-auto-flow: column;
                    &>.icon {
                        width: 18px;
                        height: 18px;
                        background: no-repeat center/contain;
                    }
                }
                &::after {
                    content: '';
                    height: 2px;
                    background-color: currentColor;
                    width: 0;
                }
                &:hover {
                    &::after {
                        width: 100%;
                    }
                }
            }
            &>li.more {
                color: #0BA59B;
                cursor: pointer;
                padding-top: 6px;
                &:hover {
                    &::after {
                        width: 0px;
                    }
                }
            }
            &>li.more>.worldie-menu>.content::after {
                display: inline-block;
                margin-left: .255em;
                vertical-align: .255em;
                content: "";
                border-top: .3em solid;
                border-right: .3em solid transparent;
                border-bottom: 0;
                border-left: .3em solid transparent;
            }
        }
    }
}

.menu-container li.more>.worldie-menu .menu-container {
    transform: translate3d(-62px, 8px, 0px);
}

//------------------menu responsive-----------------------------------
.worldie-header .search {
    display: block;
}

.worldie-header .mobile-search-icon {
    display: none;
}

@media (max-width: 768px) {
    .worldie-header .search {
        display: none;
    }
    .worldie-header .mobile-search-icon {
        display: block;
    }
}

