@import '../../scss/mixin.scss';
.project-box-component {
  position: relative;

  &::before {
    content         : '';
    position        : absolute;
    top             : 0;
    left            : 0;
    right           : 0;
    bottom          : 0;
    background-color: var(--project-box-color);
    opacity         : 0.85;
  }

  &>.content {
    position: relative;
    padding : 50px 15px;
    display : grid;
    gap     : 1rem;
    color   : white;

    &>.title {
      font-size     : 20px;
      text-align    : center;
      text-transform: uppercase;
    }

    &>.description {
      text-align: center;
      font-size : 1rem;
    }

    &>.icon {
      font-size      : 30px;
      height         : 60px;
      width          : 60px;
      margin         : 15px auto;
      line-height    : 60px;
      background     : #fff;
      border-radius  : 4%;
      box-shadow     : 0px 50px 18px -14px rgb(0 0 0 / 0%);
      transform      : translateY(0px);
      transition     : all cubic-bezier(0.45, 0.05, 0.55, 0.95) 0.3s;
      display        : flex;
      align-items    : center;
      justify-content: center;
      color          : var(--project-box-color);
    }

    &:hover {
      &>.icon {
        box-shadow   : 0px 50px 18px -14px rgb(0 0 0 / 50%);
        transform    : translateY(-30px);
        border-radius: 50%;
      }
    }
  }


}