@import '../../scss/mixin.scss';
.menu-container li .content .drop-icon {
    width: 18px;
    height: 18px;
    background: no-repeat center/contain;
}

.more.menu-container {
    li {
        color: attr(color);
        width: fit-content;
        cursor: pointer;
        &>.content {
            display: grid;
            gap: 5px;
            padding: 7px 12px;
            grid-auto-flow: column;
            &>.icon {
                width: 18px;
                height: 18px;
                background: no-repeat center/contain;
            }
        }
        &::after {
            content: '';
            height: 2px;
            background-color: currentColor;
            width: 0;
        }
        &:hover {
            &::after {
                width: 100%;
            }
        }
    }
}

.more.menu-container li:hover {
    background-color: transparent !important;
}