@import '../../scss/mixin.scss';
.worldie-home-page {
    &>.banner {
        position: relative;
        &>.background {
            display: grid;
            grid-template-columns: repeat(8, 1fr);
            &::after {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background-color: rgba($color: #000000, $alpha: 0.5);
            }
            &>li {
                &>img {
                    width: 100%;
                    height: 100%;
                    min-height: 113px;
                    object-position: center;
                    object-fit: cover;
                }
            }
        }
        &>.content {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            color: white;
            padding: 1rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
            &>.description {
                font-family: GothamRounded-Bold;
                h1 {
                    font-size: 90px;
                    text-shadow: 2px 2px 4px #000;
                    font-size: 90px;
                }
                p {
                    font-size: 40px;
                }
            }
            &>.sign-up {
                width: 300px;
                background: #f0f8ff7a;
                border-radius: 12px;
                padding: 40px 30px 15px;
                display: grid;
                gap: 1rem;
            }
        }
    }
}

.container .action {
    display: flex;
    padding: 15px;
    justify-content: center;
}

.container .row .action {
    width: 100%;
}

.posts .sec_title {
    letter-spacing: 5px;
    margin-bottom: 35px;
}

h4.sec_title {
    margin-top: 30px;
}

.dbox-donation-button {
    background: #4E2DC5 url(https://d1iczxrky3cnb2.cloudfront.net/red_logo.png) no-repeat 37px;
    color: #fff;
    text-decoration: none;
    font-family: Verdana, sans-serif;
    display: inline-block;
    font-size: 16px;
    padding: 15px 38px;
    padding-left: 75px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
    box-shadow: 0 1px 0 0 #1f5a89;
    text-shadow: 0 1px rgb(0 0 0 / 30%);
}

.dbox-donation-button:hover {
    color: #fff;
}

.title.container .search {
    max-width: 400px;
    width: 100%;
}

header.worldie-header .title {
    padding: 0px 16px;
    min-height: 50px;
}

.worldie-home-page>.banner>.content>.description {
    text-align: center;
}

@media (max-width: 992px) {
    .title.container .search {
        max-width: 280px;
    }
    .menu.container {
        max-width: 100%;
    }
    .worldie-home-page>.banner>.content>.description h1 {
        font-size: 72px;
    }
    .worldie-home-page>.banner>.content>.description p {
        font-size: 34px;
    }
    .worldie-home-page>.banner>.content>.sign-up {
        display: none;
    }
}

@media (max-width: 768px) {
    .title.container .search {
        margin-top: 5px;
        margin-bottom: 0;
    }
    .worldie-header .container {
        max-width: 100%;
    }
    header.worldie-header .title>.logo::before {
        background: no-repeat center/contain url(/assets/images/logo_small.png);
    }
    header.worldie-header .title>.logo {
        width: 50px;
    }
    header.worldie-header .title {
        padding: 0px 16px;
        min-height: 50px;
    }
    header .container {
        padding-right: 0px;
        padding-left: 0px;
    }
    header.worldie-header .title>.action>.login,
    header.worldie-header .title>.action>.sign-up {
        width: 74px;
    }
    .worldie-home-page>.banner>.content {
        background: rgba(1, 31, 130, 0.5);
        max-width: 100%;
    }
    .worldie-home-page>.banner>.content>.description {
        text-align: center;
        flex-direction: column;
        justify-content: center;
        width: 100%;
    }
    .worldie-home-page>.banner>.content>.description h1 {
        font-size: 40px;
    }
    .worldie-home-page>.banner>.content>.description p {
        font-size: 20px;
    }
}