@import '../../scss/mixin.scss';
.process-life-world-component {
    background: no-repeat center/cover url('/assets/images/fireworks.jpg');
    padding: 20px 0px;
    &>.container {
        &>.title-header {
            text-align: center;
            color: white;
            margin-bottom: 50px;
            &>h1,
            &>h3 {
                margin: 0;
            }
            &>h1 {
                font-weight: 700;
                font-family: Dosis;
                font-size: 2.5rem;
            }
            &>h3 {
                margin: 0px auto;
                font-weight: 500;
            }
        }
        &>.content {
            grid-template-columns: repeat(4, 1fr);
            display: flex;
            flex-wrap: wrap;
            border-radius: 30px;
            margin-bottom: 15px;
            overflow: hidden;
            &>.project-box-component {
                flex: 1 1 25%;
            }
        }
    }
}

@media (min-width: 600px) and (max-width: 992px) {
    .process-life-world-component>.container>.content>.project-box-component {
        flex: 1 1 50%;
    }
}

@media (max-width: 599px) {
    .process-life-world-component>.container>.content>.project-box-component {
        flex: 1 1 100%;
    }
}