@import '../../scss/mixin.scss';
    section.safe-equal {
        .h1,
        .h2,
        .h3,
        .h4,
        .h5,
        .h6,
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            margin-bottom: unset;
            font-weight: 700;
            line-height: unset;
            margin-top: 10px;
        }
        background-color: rgba(0, 0, 0, 0);
        background-blend-mode: normal;
        background: no-repeat top/cover;
        transition: background-image 3000ms linear;
        padding: 20px 0px;
        &>.container {
            &>.title {
                font-weight: 700;
                font-family: Dosis;
                font-size: 2.5rem;
                color: white;
                font-weight: bolder;
                display: flex;
                justify-content: center;
                text-align: center;
                margin-bottom: 17px;
            }
            &>.sec-tech-good-div {
                padding: 40px 30px;
                &>ul {
                    display: flex;
                    flex-wrap: wrap;
                    grid-template-columns: repeat(8, 1fr);
                    gap: 5px;
                    .menu-item {
                        width: calc(100% / 4 - 4px);
                        border: 1px solid transparent;
                        font-size: 14px;
                        font-weight: bold;
                        color: white;
                        border-radius: 30px;
                        position: relative;
                        overflow: hidden;
                        cursor: pointer;
                        &.active {
                            border-color: white;
                        }
                        &:hover {
                            border-color: white;
                        }
                        &::before {
                            content: '';
                            background-color: var(--menu-item-color);
                            position: absolute;
                            top: 0;
                            left: 0;
                            right: 0;
                            bottom: 0;
                            opacity: 0.85;
                        }
                        &>.content {
                            position: relative;
                            padding: 15px 6px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                        }
                    }
                }
                &>.live-tip {
                    margin: 2rem auto;
                    display: grid;
                    background: #7020c2a8;
                    padding: 15PX;
                    border-radius: 30PX;
                    gap: 5px;
                    grid-auto-flow: column;
                    width: fit-content;
                    font-size: 20px;
                    font-weight: bolder;
                    color: white;
                    &>.image {
                        width: 25px;
                        height: 25px;
                        background: no-repeat center/contain url('/assets/images/life-insurance.png');
                    }
                }
                &>.home {
                    display: flex;
                    gap: 30px;
                    align-items: center;
                    &>.text-content {
                        padding: 75px 15px 40px 15px;
                        color: white;
                        -webkit-text-stroke: 1px #000;
                        .blue {
                            color: blue;
                            -webkit-text-stroke: 1px #fff;
                        }
                        .red {
                            color: #EE3A79;
                            -webkit-text-stroke: 1px #fff;
                        }
                        .rocket_info {
                            display: inline-block;
                            background: no-repeat center/contain url('/assets/images/rocket.png');
                            width: 50px;
                            height: 50px;
                        }
                        .text-image {
                            display: flex;
                            align-items: self-start;
                            .life_guard_info {
                                display: inline-block;
                                width: 45px;
                                margin: 10px;
                                height: 45px;
                                background: no-repeat center/contain url('/assets/images/lifeguard.png');
                            }
                            .dolphin_guard_info {
                                display: inline-block;
                                width: 60px;
                                margin: 10px;
                                height: 60px;
                                background: no-repeat center/contain url('/assets/images/dolphin.png');
                            }
                        }
                        .animate-brand {
                            animation-name: floating;
                            animation-duration: 3s;
                            animation-iteration-count: infinite;
                            animation-timing-function: ease-in-out;
                            display: inline-block;
                        }
                        @keyframes floating {
                            from {
                                transform: translate(0, 0px);
                            }
                            65% {
                                transform: translate(0, 10px);
                            }
                            to {
                                transform: translate(0, -0px);
                            }
                        }
                        h2 {
                            align-items: center;
                        }
                        &>.action {
                            display: flex;
                            gap: 1rem;
                            grid-auto-flow: column;
                            width: 100%;
                            justify-content: left;
                            &>button {
                                border-radius: 40px;
                                overflow: hidden;
                                border: 3px solid #0ba59b;
                                cursor: pointer;
                                padding: 0;
                                width: 24%;
                                &>.content {
                                    display: flex;
                                    justify-content: center;
                                    align-items: center;
                                    color: #4463e3;
                                    font-size: 24px;
                                    -webkit-text-stroke: 1px #fff;
                                    font-weight: bolder;
                                    border: 1px solid white;
                                    border-radius: 40px;
                                    padding: 2px 5px;
                                    &>img {
                                        width: 35px;
                                        height: 35px;
                                    }
                                }
                                &::before {
                                    background-color: white;
                                }
                                &:hover {
                                    &>.content {
                                        background-color: #0ba59b;
                                    }
                                }
                            }
                        }
                    }
                    &>.image-show {
                        border-radius: 30px;
                        overflow: hidden;
                        &>img {
                            width: 100%;
                        }
                    }
                }
                &>.about {
                    padding: 35px 20px;
                    .content-box {
                        background: rgb(255 255 255 / 0.9);
                        height: 100%;
                        display: flex;
                        align-items: center;
                        border-radius: 30px;
                        justify-content: center;
                    }
                }
            }
        }
    }
    
    @media (max-width: 992px) {
        section.safe-equal>.container>.sec-tech-good-div>.home>.image-show {
            display: none;
        }
        section.safe-equal>.container>.sec-tech-good-div>.about .col-12 {
            margin-bottom: 30px;
        }
        section.safe-equal>.container>.sec-tech-good-div>.home>.text-content {
            padding: 45px 0px 20px 0px;
        }
    }
    
    @media (min-width: 991px) {
        section.safe-equal>.container>.sec-tech-good-div>ul .menu-item {
            flex: 1;
        }
    }
    
    @media (max-width: 1440px) {
        section.safe-equal>.container>.sec-tech-good-div>.home>.text-content>.action>button {
            width: 25%;
        }
    }
    
    @media (max-width: 1024px) {
        section.safe-equal>.container>.sec-tech-good-div>.home>.text-content>.action>button {
            width: 33%;
        }
    }
    
    @media (max-width: 450px) {
        section.safe-equal .container .sec-tech-good-div {
            padding: 20px 0px;
        }
        section.safe-equal>.container>.sec-tech-good-div>.home>.text-content>.action>button>.content {
            font-size: 1rem;
        }
        section.safe-equal>.container>.sec-tech-good-div>.home>.text-content>.action>button {
            width: 58%;
        }
        section.safe-equal>.container>.sec-tech-good-div>.home>.text-content>.action {
            justify-content: center;
        }
    }