@import '../../scss/mixin.scss';
.worldie-forgot-page {
    .navbar-expand .navbar-collapse {
        align-items: flex-start;
        display: flex !important;
    }
    .section-title h2.title span {
        display: block;
        font-size: 20px;
        font-weight: 100;
    }
    .show-more {
        text-align: center;
    }
    .tagline_frame .tagline h1 {
        text-transform: inherit !important;
    }
    input {
        padding: 1px 2px;
        border-width: 1px;
        border-style: inset;
    }
    .radio_btn label input[type='radio'] {
        width: auto;
        margin: 5px 3px 0 0;
    }
}