@import '../../scss/mixin.scss';
.worldie-checkbox {
  display              : grid;
  grid-template-columns: min-content 1fr;
  gap                  : 0.5rem;
  align-items          : center;
  cursor               : pointer;

  &>.icon {
    font-size      : 14px;
    color          : var(--worldie-check-color);
    display        : flex;
    align-items    : center;
    justify-content: center;
  }

  &>.label {
    user-select: none;
    color      : var(--worldie-check-label-color);
  }
}