@import '../../scss/mixin.scss';
.worldie-search {
  display         : flex;
  align-items     : center;
  background-color: white;
  border-radius   : 40px;
  padding         : 2px;
  $gap            : 5px;

  &:focus-within {
    outline: 2px solid rgba($color: #043fff, $alpha: 0.3);

  }

  &>.worldie-menu {
    margin-right: $gap;

    &>.content {

      &>.icon {
        display         : flex;
        align-items     : center;
        justify-content : center;
        border-radius   : 50%;
        cursor          : pointer;
        background-color: #043fff;
        width           : 30px;
        height          : 30px;
        color           : white;
      }
    }
  }

  &>input {
    flex: 1;
  }

  &>.search-icon {
    margin         : 0 $gap;
    display        : flex;
    align-items    : center;
    justify-content: center;
    color          : #043fff;
  }
}