@import '../../scss/mixin.scss';
.show-more-button {
    border: 1px solid white;
    padding: 5px;
    border-radius: 30px;
    cursor: pointer;
    &>.content {
        display: grid;
        grid-auto-flow: column;
        gap: 5px;
        align-items: center;
        padding: 10px 20px;
        border: 2px solid #0ba59b;
        border-radius: 30px;
        text-transform: uppercase;
        font-weight: bolder;
        color: #707070;
        &>.icon {
            color: #fff;
            width: 20px;
            height: 20px;
            background: #0ba59b;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
        }
    }
    &:hover {
        &>.content {
            background-color: #0ba59b;
            color: white;
            &>.icon {
                color: #0ba59b;
                background-color: white;
            }
        }
    }
}