@import '../../scss/mixin.scss';
.sec_slider_login {
    position: relative;
}

.sec_signup_banner_frame {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
}

.sec_signup_banner:after {
    background-image: linear-gradient(145deg, #36006d 30%, #001227 80%);
    opacity: 0.65;
}

.sec_nav.sec_nav_home.sec_nav_home_signup .login_btn {
    border-radius: 50px;
}

.worldie-login-page {
    .navbar-expand .navbar-collapse {
        align-items: flex-start;
        display: flex !important;
    }
    .section-title h2.title span {
        display: block;
        font-size: 20px;
        font-weight: 100;
    }
    .show-more {
        text-align: center;
    }
    .tagline_frame .tagline h1 {
        text-transform: inherit !important;
    }
}